
.range {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  color: #fff;
  font-size: 12px;
}
.range [class*=icon-] {
  font-size: 14px;
  margin-right: 10px;
}
.range .start {
  padding-right: 20px;
}
.range .end {
  padding-left: 20px;
}
.range .range-detail {
  width: 100%;
  background: rgba(245,245,245,0.3);
  height: 2px;
  position: relative;
}
.range .range-detail .current-progress {
  height: 2px;
}
.range .range-detail .focus {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background: #fff;
  -webkit-transform: translate3d(-7px, -7px, 0);
          transform: translate3d(-7px, -7px, 0);
  position: absolute;
  left: 90%;
}


