
.left-container {
  width: 80%;
  height: 100%;
  position: absolute;
  z-index: 40;
  background: #fff;
  top: 0;
}
.maskside {
  width: 100%;
  height: 100%;
  top: 0;
  position: absolute;
  z-index: 35;
  background: #000;
  opacity: 0.2;
}
.slideLeft-enter,
.slideLeft-leave-to {
  left: -80%;
}
.slideLeft-enter-to,
.slideLeft-leave {
  left: 0;
}
.slideLeft-enter-active,
.slideLeft-leave-active,
.mask-enter-activem,
.mask-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.mask-enter,
.mask-leave-to {
  opacity: 0;
}
.mask-enter-to,
.mask-leve {
  opacity: 0.2;
}


