
@import "/static/font-icon/style.css";
body,
html {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: rgba(240,240,240,0.941);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
  padding-top: 0;
}
body {
  padding-top: 50px;
  padding-bottom: 50px;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.loading {
  position: fixed;
  width: 100%;
  height: 100%;
  border: 1px solid #f00;
  z-index: 100;
  top: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.loading img {
  width: 200px;
  z-index: 120;
  position: absolute;
  top: 35%;
  left: 50%;
  opacity: 0.8;
  margin-left: -100px;
}
.loading .loadingmask {
  background: #000;
  opacity: 0.2;
  width: 100%;
  height: 100%;
  top: 0;
  position: absolute;
  z-index: 110;
}
.fade-loading-enter-active {
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
.fade-loading-leave-active {
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.fade-loading-enter,
.fade-loading-leave-to {
  opacity: 0;
}
.fade-loading-enter-to,
.fade-loading-leave {
  opacity: 0.2;
}


