
.header {
  position: fixed;
  border: 1px solid #f00;
  height: 50px;
  width: 100%;
  top: 0;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background: #c62f2f;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.header .icon-music,
.header .icon-wangyiyun,
.header .icon-community {
  font-size: 26px;
  color: #ccafaf;
}
.header .icon-menu,
.header .icon-search {
  color: #fff;
  font-size: 26px;
  padding: 0 10px;
}
.header .tabmenu {
  margin: 0px auto;
}
.header .tabmenu i {
  margin: 0 2px;
}
.header .tabmenu i.router-link-active i {
  color: #fff;
}
.header .tabmenu i.active {
  color: #4ae0a8;
}


